<template>
    <div>
        <Header></Header>
        <div class="content" v-html="content">
            
            {{content}}
        </div>
    </div>
</template>
<script>
import header from './header'
export default {
    name:'newsDetails',
    data(){
        return{
            id: this.$route.params.id,
            content: ''
        }
    },
    components:{
        'Header': header
    },
    created(){
        var vm = this;
        var url = window.location.href;
        var id = url.split(':')[3];
        vm.$http({
            url: 'https://zhihu-daily.leanapp.cn/api/v1/contents/'+this.id,
            method: 'get'
        }).then(res=>{
            this.content = res.data.CONTENTS.body;
            console.log(res);
        })
     
    }
}
</script>
<style scoped>
@import '../../static/css/details.css';
.content >>>.content img{
    width: 100%;
}
.content{
    margin-top: 0.65rem;
    font-size: 0.3rem;
    padding: 0.3rem;
}
</style>



