<template>
    <div class="lazyload">

        <mt-loadmore  :bottom-method="loadBottom" >
            <ul>
                <li  v-for="(item,index) in imgs" :key="index">
                    {{item.body}}
                </li>
            </ul>
        </mt-loadmore>
    </div>
</template>
<script>
import { Lazyload, Header, Loadmore} from 'mint-ui'
import Vue from 'vue'
import header from './header'
Vue.component(Loadmore.name, Loadmore);
Vue.use(Lazyload);
Vue.component(Header.name, Header);
Vue.component('Header', header);
export default {
    data(){
        return{
            imgs: []
        }
    },
    mounted() {
        this.$http({
            url: 'http://jsonplaceholder.typicode.com/posts',
            method: 'get'
        }).then(res=>{
            console.log(res);
           this.imgs = res.data
        })
    },
    methods: {
        loadBottom(){
          
        }
    },
}
</script>
<style scoped>
.lazyload{
    font-size: 0.35rem;
}
ul{
    margin-top: 0.5rem;
}
img{
    width: 3rem;
    height: 3rem;
}
</style>



