<template>
<div class="hello">
  <Header></Header>
  <div class="box">   
    <scroller class="scroller" :on-refresh="refresh" :on-infinite="infinite" ref="myscroller">
      <div class="container">  
        <ul class="content" v-for="(item, index) in response" :key="index">
          <li id="linkto" @click="linkto(item.id)">{{item.title}}</li>
          <img :src='item.images[0]' >
        </ul>
      </div> 
    </scroller>
  </div>
  </div>
</template>

<script>
import Vue from 'vue'
import { Header, Button} from 'mint-ui'
import header from './header'
import VueScroller from 'vue-scroller'
Vue.component(Button.name, Button)
Vue.component(Header.name, Header)
Vue.use(VueScroller)
export default {
  name: 'HelloWorld',
  data () {
    return {
      response: [],
      time: 1
    }
  },
  components:{
    Header: header
  },
  mounted() {
    this.getData();
    this.beforeData();
    console.log(this.$refs.myscroller)
  },
  methods: {
       getData(){
      this.$http({
                method: 'get',
                url:'https://zhihu-daily.leanapp.cn/api/v1/last-stories',          
            }).then(res=>{
                this.response = this.response.concat(res.data.STORIES.stories)
            })
    },

    beforeData(){
      var date = new Date();
      var year = date.getFullYear();
      var month = date.getMonth()+1;
      var day = date.getDate();
      if(month<9){
        month = '0' + month; 
      }
      if(day <9){
        day = '0' + day;
      }
      var before = year+''+month+day - this.time;
      if(before != '20190601'){
        this.$http({
                method: 'get',
                url:'https://zhihu-daily.leanapp.cn/api/v1/before-stories/'+before,          
            }).then(res=>{
                this.response = this.response.concat(res.data.STORIES.stories);
                this.time ++;
          })
      }
      else{
      }
      
    },
    linkto(id){
      this.$router.push({path:'/details/'+id})
    },
    infinite(done) {
    
      var vm = this;
      setTimeout(()=>{
          vm.beforeData();
          done();
        },1000)
    },
    refresh() {
      this.response =[];
      this.time = 1;
      setTimeout(() => {
        this.getData();
        this.$refs.myscroller.finishPullToRefresh();
      }, 500);
    }

  },
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.box{
  overflow-y: scroll;
  touch-action: pan-y;
}
.container{
  padding-top: 1rem;
}

#linkto{
  cursor: pointer;
}
#linkto:hover{
  color: orange;
}
.swipe{
  width: 100%;
  height: 4rem;;
  background: pink
}
li{
  font-size: 0.35rem;
}
.content{
  display: flex;
  justify-content: space-between;
  margin-bottom: 0.3rem;
  padding-left: 0.2rem;
  padding-right: 0.2rem;
}
.content li{
  padding-top: 0.5rem;
}
.content >>>img{
  width: 2rem;
  height: 2rem;
}
</style>



