import Vue from 'vue'
import Router from 'vue-router'
import HelloWorld from '@/components/HelloWorld'
import lazyload from '@/components/lazyload'
import details from '@/components/details'
Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'HelloWorld',
      component: HelloWorld
    },
    {
      path: '/lazyload',
      name: 'lazyload',
      component: lazyload
    },
    {
      path: '/details/:id',
      name: 'details',
      component: details
    }
  ]
})



