<template>
    <div>
    <mt-header class="mtheader"  fixed title="知乎日报">
        <div slot="left">
            <mt-button v-show="backShow"  icon="back"> <router-link class="link" to="/">返回</router-link></mt-button>
        </div>
    </mt-header>
    </div>
</template>
<script>
export default {
    data(){
        return{
            backShow: false
        }
    },
    mounted() {
      this.btnShow();
    }, 
    methods: {
        btnShow(){
            var url = window.location.hash;
            if(url == '#/'){
                this.backShow = false;
            }  
            else{
                this.backShow = true;
            }
        }
    },
}
</script>
<style scoped>
.mtheader{
    background: skyblue;
}
.link{
    color: #fff;
    text-decoration: none;
}
</style>



